import { useState } from "react";
import { Users, Package, LogOut, ShoppingBag, ChevronLeft, ChevronRight } from "lucide-react";
import {
  Sidebar,
  SidebarContent,
  SidebarGroup,
  SidebarGroupContent,
  SidebarGroupLabel,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
} from "@/app/Components/ui/sidebar";

interface DashboardSidebarProps {
  currentSection: "products" | "users";
  onSectionChange: (section: "products" | "users") => void;
  onLogout: () => void;
}

export function DashboardSidebar({ currentSection, onSectionChange, onLogout }: DashboardSidebarProps) {
  const [collapsed, setCollapsed] = useState(false);

  const menuItems = [
    { title: "Products", key: "products" as const, icon: Package, description: "Manage your products" },
    { title: "Users", key: "users" as const, icon: Users, description: "Manage users" },
  ];

  return (
    <>
      {/* Sidebar */}
      <Sidebar
        className={`fixed top-0 left-0 h-screen z-50 transition-all duration-300
          ${collapsed ? "w-14 bg-gray-800" : "w-60 bg-gradient-to-b from-orange-500 via-orange-600 to-orange-700"} 
          text-white
        `}
      >
        {/* Sidebar Header */}
        <div className={`p-4 border-b ${collapsed ? "border-gray-700" : "border-orange-600"}`}>
          <div className="flex items-center gap-3">
            <div className="p-2 rounded-lg bg-white/20">
              <ShoppingBag className={`h-6 w-6 ${collapsed ? "text-gray-200" : "text-white"}`} />
            </div>
            {!collapsed && (
              <div>
                <h2 className="text-lg font-bold text-white">Admin Panel</h2>
                <p className="text-xs text-white/80">E-Commerce Dashboard</p>
              </div>
            )}
          </div>
        </div>

        {/* Sidebar Content */}
        <SidebarContent>
          <SidebarGroup>
            <SidebarGroupLabel className={`text-white/70 ${collapsed ? "hidden" : ""}`}>
              Main Navigation
            </SidebarGroupLabel>
            <SidebarGroupContent>
              <SidebarMenu>
                {menuItems.map((item) => (
                  <SidebarMenuItem key={item.key}>
                    <SidebarMenuButton
                      onClick={() => onSectionChange(item.key)}
                      className={`w-full justify-start gap-3 transition-all rounded-lg
                        ${currentSection === item.key
                          ? "bg-white/20 text-white shadow-md"
                          : "text-white hover:bg-white/10 hover:text-white"}
                      `}
                    >
                      <item.icon className="h-5 w-5 flex-shrink-0" />
                      {!collapsed && (
                        <div className="flex flex-col items-start">
                          <span className="font-medium">{item.title}</span>
                          <span className="text-xs opacity-70">{item.description}</span>
                        </div>
                      )}
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                ))}
              </SidebarMenu>
            </SidebarGroupContent>
          </SidebarGroup>

          <SidebarGroup className="mt-auto">
            <SidebarGroupContent>
              <SidebarMenu>
                <SidebarMenuItem>
                  <SidebarMenuButton
                    onClick={onLogout}
                    className="w-full justify-start gap-3 rounded-lg text-white hover:bg-red-600 hover:text-white transition-all"
                  >
                    <LogOut className="h-5 w-5 flex-shrink-0" />
                    {!collapsed && <span>Logout</span>}
                  </SidebarMenuButton>
                </SidebarMenuItem>
              </SidebarMenu>
            </SidebarGroupContent>
          </SidebarGroup>
        </SidebarContent>

        {/* Toggle Button */}
        <button
          onClick={() => setCollapsed(!collapsed)}
          className="absolute -right-4 top-4 z-50 w-8 h-8 bg-white text-orange-600 rounded-full flex items-center justify-center shadow hover:bg-gray-100 transition"
        >
          {collapsed ? <ChevronRight className="h-4 w-4" /> : <ChevronLeft className="h-4 w-4" />}
        </button>
      </Sidebar>

      {/* Main content placeholder */}
      <div className={`transition-all duration-300 ${collapsed ? "ml-14" : "ml-60"}`}>
        {/* Your main content goes here */}
      </div>
    </>
  );
}
