import { useState } from "react";
import { SidebarProvider, SidebarTrigger } from "@/app/Components/ui/sidebar";
import { DashboardSidebar } from "@/app/Components/dashboard/DashboardSidebar";
import { Users, Package, BarChart3, LogOut } from "lucide-react";
import { Button } from "@/app/Components/ui/button";
import { useToast } from "@/app/hooks/use-toast";

interface DashboardLayoutProps {
  children: React.ReactNode;
  currentSection: "products" | "users";
  onSectionChange: (section: "products" | "users") => void;
  onLogout: () => void;
}

const DashboardLayout = ({ children, currentSection, onSectionChange, onLogout }: DashboardLayoutProps) => {
  const { toast } = useToast();

  const handleLogout = () => {
    toast({
      title: "Logged out",
      description: "You have been successfully logged out.",
    });
    onLogout();
  };

  return (
    <SidebarProvider>
      <div className="min-h-screen w-full flex bg-background">
        <DashboardSidebar 
          currentSection={currentSection}
          onSectionChange={onSectionChange}
          onLogout={handleLogout}
        />
        
        <div className="flex-1 flex flex-col">
          {/* Header */}
          <header className="h-16 border-b border-border bg-card flex items-center justify-between px-6">
            <div className="flex items-center gap-4">
              <SidebarTrigger className="text-foreground" />
              <div>
                <h1 className="text-xl font-semibold">E-Commerce Dashboard</h1>
                <p className="text-sm text-muted-foreground">
                  {currentSection === "products" ? "Manage your products" : "Manage your users"}
                </p>
              </div>
            </div>
            
            <div className="flex items-center gap-4">
              <div className="text-right">
                <p className="text-sm font-medium">Admin User</p>
                <p className="text-xs text-muted-foreground">admin@example.com</p>
              </div>
              <Button
                variant="outline"
                size="sm"
                onClick={handleLogout}
                className="gap-2"
              >
                <LogOut className="h-4 w-4" />
                Logout
              </Button>
            </div>
          </header>

          {/* Main Content */}
          <main className="flex-1 p-6 fade-in">
            {children}
          </main>
        </div>
      </div>
    </SidebarProvider>
  );
};

export default DashboardLayout;
